


































import {Vue} from 'vue-property-decorator';
import Dygraph from 'dygraphs';
import {origins} from '@/origins';
import PerSeriesOptions = dygraphs.PerSeriesOptions;
import {indoorTiers} from '@/indoor23';
import {outdoor23nj} from '@/outdoor23nj';
import onMobile from '@/onMobile';

export default class Home extends Vue {
  private graph: Dygraph | undefined;
  private lables = new Map<string, number>();
  private mode = 0;
  private tiers: { [key: string]: { [key: string]: string[] } } = {};
  private tier: string = 'kl';
  private group: string = '';
  private includeParents = true;
  private nameFilter = '';
  private placeFilter = '';
  private theOrigins = new Map<string, string[]>();
  private  series: {
    [seriesName: string]: PerSeriesOptions,
  } = {};

  public reset() {
    this.tier = 'kl';
    this.nameFilter = '';
    this.placeFilter = '';
    if (this.mode === 0) {
      this.nameFilter = '';
      this.placeFilter = '';
    } else {
      this.tiers = this.mode === 1 ? indoorTiers : outdoor23nj;
      this.tier = Object.keys(this.tiers)[0];
      this.group = '';
    }
  }

  public mounted() {
    this.graph = new Dygraph('graph', 'graph2.csv', {
      connectSeparatedPoints: true,
      strokeWidth: 0.5,
      highlightSeriesOpts: {
        strokeWidth: 1.5,
        strokeBorderWidth: 1,
        highlightCircleSize: 5,
      },
      interactionModel: onMobile() ? {
        mousedown(event: MouseEvent, g: Dygraph, context: any) {
          context.initializeMouseDown(event, g, context);
        },
      } : null,
      legend: 'always',
      labelsShowZeroValues: false,
      series: this.series,
      stepPlot: true,
      valueRange: [750, 2320],
    });
    for (const origin of origins.split('\n')) {
      const os = origin.split(',');
      this.theOrigins.set(os[0], os.slice(1));
    }
    this.graph.ready(() => {
      this.graph!!.getLabels().forEach((l, i) => {
        // if (i !== 0)
          this.lables.set(l, i - 1);
      });
    });
  }

  private refilter() {
    if (this.graph) {
      const sss = new Set<number>();
      const vs = this.graph.visibility();
      if (this.mode === 0) {
        this.graph.getLabels().forEach((t, i) => {
          if (i !== 0 && t !== 'sd' && t !== 'teams') {
            const show = new RegExp(`^.*${this.nameFilter}.*\\(.*${this.placeFilter}.*\\)`, 'i').test(t);
            if (show) {
              if (this.includeParents) {
                for (const s of this.theOrigins.get(t)!!) {
                  sss.add(this.lables.get(s)!!);
                }
              } else {
                sss.add(i - 1);
              }
            }
          }
        });
      } else {
        const tier = this.tiers[this.tier]!!;
        for (const [p, ts] of Object.entries(tier)) {
          for (const t of ts) {
            if (this.group && this.group !== p) {
              continue;
            }

            const i = this.lables.get(t)!!;
            if (this.includeParents) {
              for (const s of this.theOrigins.get(t)!!) {
                sss.add(this.lables.get(s)!!);
              }
            } else {
              sss.add(i);
            }
          }
        }
      }
      const ss: number[] = [];
      const hs: number[] = [];

      vs.forEach((b, i) => {
        const bb = sss.has(i);
        if (b !== bb) {
          (bb ? ss : hs).push(i);
        }
      });

      // @ts-ignore
      this.graph.setVisibility(ss, true);
      // @ts-ignore
      this.graph.setVisibility(hs, false);
    }
  }

}
